import React, { useState } from 'react';
import ProjectCard from './ProjectCard';
import ProjectModal from './ProjectModal';
import gameJamProjects from '../data/gameJamProjects';

export default function GameJamList() {
	const [selected, setSelected] = useState(null);

	return (
		<section className="max-w-8xl mx-auto px-4 py-2">
			<h2 className="text-4xl font-bold text-white mb-3 text-center">Game Jams</h2>
			<hr className="my-2 border-t-2 border-red-400 mx-auto w-1/2" />

			<div className="grid gap-4 sm:grid-cols-2 lg:grid-cols-3 p-6 mx-auto max-w-[1600px] justify-center justify-items-center">
				{gameJamProjects.map((project, index) => (
					<ProjectCard
						key={index}
						project={project}
						onClick={() => setSelected(project)}
					/>
				))}
			</div>

			{selected && (<ProjectModal project={selected} onClose={() => setSelected(null)} isOpen={true} />)}
		</section>
	);
}