const skills = [
    { name: 'Unity', level: 'Advanced' },
    { name: 'C#', level: 'Advanced' },
    { name: 'Microsoft Office', level: 'Advanced' },
    { name: 'React', level: 'Intermediate' },
    { name: 'Tailwind', level: 'Intermediate' },
    { name: 'Lua', level: 'Intermediate' },
    { name: 'Roblox Studio', level: 'Intermediate' },
    { name: 'Blender', level: 'Intermediate' },
    { name: 'Jira', level: 'Intermediate' },
    { name: 'Trello', level: 'Intermediate' },
];

export default function Skills() {
    return (
        <section className="max-w-4xl mx-auto px-4 py-8">
            <h2 className="text-4xl font-bold text-white mb-3 text-center">Skills</h2>
			<hr className="my-2 border-t-2 border-red-400 mx-auto w-1/2" />
            <ul className="grid grid-cols-4 sm:grid-cols-5 gap-3 p-6">
                {skills.map(({ name, level }) => (
                    <li
                        key={name}
                        className="bg-gray-700 rounded-md p-2 text-white"
                    >
                        <h3 className="text-lg font-semibold mb-0.5">{name}</h3>
                        <p className="text-gray-300 italic text-sm">{level}</p>
                    </li>
                ))}
            </ul>
        </section>
    );
}